.popup__input {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  min-height: 27px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  padding: 0;
  padding-bottom: 10px;
  box-sizing: border-box;
  margin-bottom: 5px;
}

.popup__input:focus {
  outline-style: none;
}